@import '../variables.scss';
@import '../mixins.scss';

.list {
  @include list-reset;
  background: linear-gradient(0deg, #98ffd4 -50%, rgba(255, 255, 255, 0) 71.25%),
    linear-gradient(180deg, #ffffff 0%, rgba(255, 255, 255, 0) 100%);
  border-radius: 42.484px;
  padding: 27px 37px;

  input {
    outline: none;
    border: none;
    background: #ffffff;
    border-radius: 22px;
    padding: 10px 20px;
    font-size: 16px;
    line-height: 16px;
    width: 100%;
    margin-bottom: 10px;
    color: #326799;
  }

  input:focus {
    box-shadow: 0px 0px 3px 3px rgba(49, 103, 150, 0.5);
  }

    textarea {
    outline: none;
    border: none;
    background: #ffffff;
    border-radius: 22px;
    padding: 20px;
    font-size: 16px;
    line-height: 16px;
    color: #326799;
    margin-right: 18px;
    transition: 0.3s;
    width: 100%;
  }

  textarea:focus {
    box-shadow: 0 0 3px 3px rgba(47, 83, 116, 0.4);
  }
  
}

.item:last-child input {
  margin-bottom: 0;
}
